@charset "UTF-8";

/**
 * Основные стили
 **/

html {
	font-size: 16px;
}

body {
	font-family: 'Roboto', 'Arial', sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word;
}

.container {
	@include min-max(320px, 100%, 0);
}

.wrap {
	@include min-max(320px, 1170px, 15px);
}

.button-green {
	background: linear-gradient(to bottom, #00ba5d, #006b35);
	-webkit-border-radius: 7px;
	-moz-border-radius: 7px;
	border-radius: 7px;
	color: #ffffff;
	border: none;
	border-bottom: 5px solid #004a20;
	cursor: pointer;
	padding: 12px 35px 12px;
	box-shadow: 0 0 38px 20px rgba(#00af57, 0.18);

	&:hover {
		background: linear-gradient(to bottom, #00e070, #007f3f);
	}
}