.header {
  background: url("../../img/header-bg.jpg") top center no-repeat;
  padding-top: 15px;
  padding-bottom: 15px; }
  .header__topline {
    display: flex;
    align-items: center;
    margin-bottom: 40px; }
  .header__logo {
    margin-right: 60px; }
  .header__menu ul {
    display: flex;
    align-items: center;
    list-style-type: none;
    margin: 0;
    padding: 0; }
    .header__menu ul a {
      display: block;
      color: #000000;
      font-size: 14px;
      text-decoration: none;
      margin-right: 35px;
      margin-left: 35px; }
      .header__menu ul a:hover {
        color: green; }
  .header__main {
    width: 500px; }
    .header__main h1 {
      font-family: 'Playfair Display', serif;
      font-size: 70px;
      color: #432918;
      margin: 0 0 45px;
      font-weight: 300; }
  .header__text {
    width: 365px; }
    .header__text p {
      margin: 0 0 20px;
      line-height: 1.35; }
      .header__text p span {
        color: #006633;
        white-space: nowrap; }
  .header__list {
    margin: 0 0 45px;
    padding: 0;
    list-style-type: none; }
    .header__list li {
      margin-bottom: 25px;
      position: relative;
      padding-left: 90px;
      padding-top: 5px;
      font-size: 16px;
      color: #000000; }
      .header__list li span {
        color: #006633;
        font-weight: 700; }
      .header__list li:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0; }
      .header__list li:first-of-type:before {
        background: url(../../img/header-item1.jpg);
        width: 71px;
        height: 53px; }
      .header__list li:nth-of-type(2):before {
        background: url(../../img/header-item2.jpg);
        width: 65px;
        height: 62px; }
      .header__list li:nth-of-type(3):before {
        background: url(../../img/header-item3.jpg);
        width: 62px;
        height: 60px; }
  .header__button {
    margin-top: 35px; }
